<template>
    <div>
         <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
    <!--  -->
        <el-card>
        <!--  -->
        <el-row :gutter="20">
            <el-col :span="8">
                <el-input placeholder="" v-model="paramsInfo.query" clearable @clear="getOrderList()">
                    <el-button slot="append" icon="el-icon-search" @click="getOrderList()"></el-button>
                </el-input>
            </el-col>
        </el-row>
        <!--  -->
        <el-table :data="orderList"  border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column  prop="order_number"  label="" ></el-table-column>
            <el-table-column  prop="order_price"  label="" width="120"></el-table-column>
            <el-table-column  prop="pay_status"  label="" width="80">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.pay_status === '0'" type="warning"></el-tag>
                    <el-tag v-else type="success"></el-tag>
                </template>
            </el-table-column>
            <el-table-column  prop="is_send"  label="" width="80"></el-table-column>
            <el-table-column  prop="create_time"  label="" width="160">
                <template slot-scope="scope">
                    {{ scope.row.create_time | dateFormat}}
                </template>
            </el-table-column>
            <el-table-column   label=""  width="120">
                <template slot-scope="scope">
                    <!--  -->
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox">
                    </el-button>
                    <!--  -->
                    <el-button type="success" icon="el-icon-location" size="mini" @click="showProcessBox">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="paramsInfo.pagenum"
                :page-sizes="[10,20,50,100]"
                :page-size="paramsInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="setAddressDialogVisible" width="50%" @close="setCateDialogVisibleClosed">
            <el-form :model="addAddressForm" :rules="addAddressrules" ref="addAddressFormRef" label-width="100px">
                <el-form-item label="/" prop="address1">
                    <el-cascader v-model="addAddressForm.address1" :options="cityData"  clearable :props="{ expandTrigger: 'hover' }">
                  </el-cascader> 
                </el-form-item>
                <el-form-item label="" prop="address2">
                  <el-input v-model="addAddressForm.address2" ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
            <el-button @click="setAddressDialogVisible = false"> </el-button>
            <el-button type="primary" @click=""> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="setProcessDialogVisible" width="50%">
                <el-timeline >
                    <el-timeline-item
                    v-for="(activity, index) in processInfo"
                    :key="index"
                    :timestamp="activity.time">
                    {{activity.context}}
                    </el-timeline-item>
                </el-timeline>
        </el-dialog>
    </div>
</template>

<script>

import cityData from './citydata.js'

export default {

  data() {
    return {
        /*  */
        paramsInfo:{
            pagesize:10,
            pagenum:1,
            query:''
        },
        /*  */
        orderList:[],
        total:0,
        /*  */
        setAddressDialogVisible:false,
        addAddressForm:{
            address1:[],
            address2:''
        },
        addAddressrules:{
        address1: [
          { required: true, message: '/', trigger: 'blur' },
        ],
        address2:[
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
        },
        cityData:cityData,
        /*  */
        setProcessDialogVisible:false,
        processInfo:[]
    }
  },

  created() {
    this.getOrderList()
  },

  methods: {
    async getOrderList(){
        const {data:res} = await this.$http.get('orders',{params: this.paramsInfo})
        if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
        console.log(res);
        this.$message.success('')
        this.orderList = res.data.goods
        this.total = res.data.total
    },
    /*  */
    handleSizeChange(newSize) {
      this.paramsInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.paramsInfo.pagenum = newPage
      this.getOrderList()
    },
    /*  */
    showBox(){
        this.setAddressDialogVisible = true
    },
    setCateDialogVisibleClosed(){
        this.$refs.addAddressFormRef.resetFields()
    },
    /*  */
    async showProcessBox(){
        const {data:res} = await this.$http.get('/kuaidi/1106975712662')
        if(res.meta.status !== 200) return this.$message.error('')
        this.processInfo = res.data
        this.setProcessDialogVisible = true
    }
  }

}
</script>

<style lang="less" scoped>
.el-cascader{
    width: 100%;
}
</style>
